import React, { Component } from 'react';

class InputNumber extends Component {

    state = { 
        value:''
     }

     style = {
         visibility: 'hidden',
     }

    render() { 
        return ( 
            <div className="container formGroup w-50" style={this.style}>
                <div className="my-2">
                    <input className="formControl" type="number" id="number" placeholder="saisissez votre numero"/>
                </div>
                <div className="my-2">
                    <input className="btn btn-primary" type="button" id="buttonN" value="Commencer la transaction"/>
                </div>
            </div>
         );
    }
}
 
export default InputNumber;