import logo from './favico.png';
import './App.css';
import Om from './components/om/om';
import InputNumber from './components/number/inputNumber';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <Om/>
        <InputNumber/>
      </header>
    </div>
  );
}

export default App;
