import React, { Component } from 'react';
import om from './om.png';

class Om extends Component {
    state = { 
        src: om,
        alt: "logo orange money"
     }

    render() { 
        return (
            <div className="container btn">
                <img src={this.state.src} alt={this.state.alt}/>
            </div>
        );
    }
}
 
export default Om;